.main {
    width: min(calc(100% - 0.8em), 800px);
    height: 100%;
    min-height: 440px;
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: max(60px, 10vh) 1fr 60px;
    row-gap: 10px;
    padding: 0 0.4em;
    grid-area: 1 /1 /-1 /-1;
}
.header > p , .header > h1 {
    margin: 0;
}
.header {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: var(--my-dark-blue);
    font-size: 0.7rem;
    grid-area: 1 / 1 / 2 / -1;
}
.body {
    display: grid;
    grid-template-columns: 20% 60% 20%;
    grid-template-rows: auto;
    color: var(--my-blue);
    width: 100%;
    grid-area: 2 / 1 / 3 / -1;
}
.data {
    grid-area: 1 /1 / 2/3;
}
.contact-photo {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 5px 0;
}
.form {
    grid-area: 1 / 2 / 2 / 3;
    width: 100%;
    height: max(150px, 30%);
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap: 5px 20px ;
}
.moreInfoSection {
    margin-top: 10px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    gap: 5px;
    font-size: 0.8em;
}
.bold {
    font-weight: 600;
}
.small {
    font-size: 0.9em;
    font-weight: 200;
}
.contact-buttons {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    gap: min(3vh, 10px);
}
.moreInfo-button {
    padding: 3px 6px;
    color: var(--my-blue);
    background-color: var(--my-white);
    border: 0.8px solid var(--my-blue);
    border-radius: 55px;
    display: flex;
    justify-content: space-around;
    text-align: center;
    align-items: center;
    gap: 4px;
    font-weight: 900;
    cursor: default;
    font-size: 0.7rem;
    text-shadow: 0;
    cursor: pointer;
}

.plus-sign {
    width: 16px;
    height: 16px;
    border: 1px solid var(--my-blue);
    border-radius: 50%;
    font-size: 1.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.whole-preview {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-end;
    align-items: flex-end;
    gap: 5px;
}
.foot {
    width: 100%;
    height: 100%;
    grid-area: 3 / 1 / 4 / -1;
}
.main-container {
    width: 100%;
    display: grid;
    justify-items: center;
    align-items: center;
    grid-template-columns: 100%;
    grid-template-rows: 100%;
}

/* contact page */
.contact-header {
    display: block;
}
.contact-header h1{
    margin-bottom: 10px;
}

/* intro pages */
.intro-main {
    grid-template-rows: 30px max(300px,35vh) 60px;
    row-gap: 25px;
}
.intro-main h1{
    margin-top: 0;
    margin-bottom: 5px;
    font-size: 3rem;
}
.intro-main p.intro-p {
    margin-bottom: 40px;
    line-height: 22px;
    font-size: 0.9em;
}
.intro-main .small {
    font-size: 0.5em;
    font-weight: 900;
}

/* other main pages */
.others-form {
    grid-area: 1 / 1 / 2 / 4;
}
.others-title {
    width: 100%;
}
.others-title > p{
    margin-top: 0;
    width: 80%;
}
.others-main {
    grid-template-rows: auto 1fr 60px;
    grid-area: 1 / 1 / 2 / 2;
}
/* description page */
.desc-main {
    grid-template-rows: auto 1fr 60px;
    row-gap: 0;
}
.desc-title {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}
.desc-title h1 {
    margin: 0;
    font-size: 1.4rem;
    font-weight: 600;
}
.desc-title p{
    margin-bottom: 0;
}
.desc-header .preview-button {
    margin-top: 10px;
}
.desc-body {
    margin-top: 3vh;
    margin-bottom: 20px;
    width: 100%;
    height: min(55vh, 300px);
}
/* summary pages */
.summary-data {
    display: flex;   
    flex-direction: column;
}
.data-summary {
    width: 90%;
    min-height: 70px;
    max-height: 80px;
    border: 0.5px solid var(--my-grey);
    border-radius: 6px;
    display: grid;
    grid-template-rows: 100%;
    grid-template-columns: 6% 90%;
    column-gap: 4%;
    overflow: hidden;
}
.summary-id {
    grid-area: 1 / 1 / 2 /2;
    width: 100%;
    aspect-ratio: 1 / 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    font-size: 0.8em;
    background-color: var(--my-cream);
    border-right: 0.5px solid var(--my-grey);
    border-bottom: 0.5px solid var(--my-grey);
    border-radius: 0 0 8px 0;
}
.data-summary-main {
    align-self: center;
    grid-area: 1 / 2 / 2 / 3;
    width: 100%;
    height: 80%;
    display: grid;
    grid-template-columns: 80% 20%;
    grid-template-rows: 50% 50%;
    justify-content: center;
    gap: 5px;
}
.summary-title {
    font-size: 0.8em;
    display: flex;
    align-items: center;
}
.summary-title .bold {
    font-size: 1em;
}
.add-description {
    color: var(--my-light-blue);
    font-size: 0.8em;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
    gap: 4px;
    font-weight: 600;
}

.plus {
    font-size: 1.6em;
    text-decoration: none;
    font-weight: 900;
}
.summary-description {
    overflow: hidden;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    font-size: 0.8em;
    color: black;
}
.summary-description ul {
    margin-top: 0;
    padding-left: 20px;
}
.summary-buttons {
    display: flex;
    justify-content: center;
}
.summary-button {
    padding: 0;
    aspect-ratio: 1 /1;
    border: none;
    background-color: inherit;
    color: var(--my-light-blue);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.add-data-container {
    width: 90%;
    border: 1px dashed var(--my-light-blue) ;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
    height: 40px;
    margin-top: 20px;
    cursor: pointer;
}
.add-data-button {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.7em;
    color: var(--my-light-blue);
    font-weight: 600;
}
 /* education main-page */
.educate-body {
   grid-template-columns: 100%;
   grid-template-rows: auto auto;
   row-gap: 40px;
}
.educate-desc-container{
    padding: 20px;
    padding-top: 10px;
    box-shadow: 1px 1px 10px var(--my-grey);
    border-radius: 6px;
    gap: 10px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    transition: height cubic-bezier(0.215, 0.610, 0.355, 1) 0.5s;
}
.educate-desc-container.open{
    height: min(500px, 80vh);
}
.educate-desc-container > div > span > svg {
    transition: transform ease-out 0.5s;
}
.educate-desc-container.open > div > span > svg {
    transform: rotate(180deg);
}
.educate-desc-title {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}
.educate-desc-title span{
    word-spacing: 2px;
    font-weight: 800;
    font-size: 0.7em;
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 5px;
    border-radius: 6px;
    cursor: pointer;
}
.educate-desc-tip {
    background-color: #e9f4f6;
    padding: 10px 20px;
    border-radius: 6px;
    border: 0.5px solid var(--my-grey);
    font-size: 0.7em;
    font-weight: 600;
}
.desc-tip-head {
    display: grid;
    grid-template-rows: 100%;
    grid-template-columns: 3.5% 1fr;
}
.educate-desc-tip ul {
    font-weight: 100;
}
.educate-desc-container.close {
    height: 170px;
    overflow: hidden;
    gap: 0;
    padding-bottom: 5px;
    padding-top: 5px;
}
.educate-desc-container .textEditor-container {
    transition: all 0.2s;
}
.educate-desc-container.open .textEditor-container {
    visibility:visible;
    height: 100%;
}
.educate-desc-container.close .textEditor-container {
    height: 0;
    visibility: hidden;
} 
.italic {
    font-style: italic;
}
/* final page */
.final-header {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 0.3em;
}
.final-body {
    grid-template-columns: 60% 35%;
    justify-content: space-between;
}
.save-options {
    grid-area: 2 / 1/ 3/ 2;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 0.5em;
}
.save-options > div {
    width: 2em;
    aspect-ratio: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--my-green);
    border-radius: 50%;
    color: var(--my-white);
    cursor: pointer;
}
.main-preview-container {
    display: grid;
    grid-template-columns: 8% 1fr;
    grid-template-rows: 5% 1fr;
    column-gap: 0.6em;
}
.main-preview-container .preview{
    box-shadow: 0 -3px 7px 0 var(--my-grey);
    grid-area: 2 / 2 / 3/ 3;
}
.pdf-edit-section {
    grid-area: 1 / 2 / 2 / 3;
    display: flex;
    justify-content: space-between;
    font-size: 0.8em;
}
.paper-size {
    position: relative;
    display: flex;
    margin-right: 2.5em;
}
.paper-size > span {
    position: absolute;
    left: 100%;
    top: 0;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    height: 1.3em;
    transition: height 0.4s;
    overflow: hidden;
    z-index: 1;
    padding: 0 .2em;
}
.paper-size > span.open {
    background-color: var(--my-light-blue);
    color: var(--my-cream);
    border-radius: 5px;
    height: 4em;
    padding: 0.2em;
}
.paper-size svg {
    transition: transform  0.5s;
}
.paper-size svg.open {
    transform: rotate(180deg);
    color: var(--my-cream) !important;
}
.paper-size .bold {
    font-size: 1em;
    width: max-content;
}
.edit-cv-section{
    padding: 1em;
    font-size: 0.7em;
}
.cv-editBox {
    display: flex;
    flex-direction: column;
    border: 1px solid var(--my-grey);
    border-radius: 15px;
    overflow: hidden;
    margin-top: 20%;
}
.cv-editBox > div {
    padding: 1em;
    display: grid;
    grid-template-columns: 10% 1fr 10%;
    background-color: var(--my-cream);
    border-bottom: 1px solid var(--my-grey);
}
.cv-editBox > div:not(.edit-container){
    cursor: pointer;
    font-weight: 600;
}

.cv-editBox :last-child {
    border-bottom: none;
}
.edit-container {
    background-color: inherit !important;
    display: flex !important;
    flex-direction: column;
    gap: 1em;
}
.openingArrow-container{
    display: grid;
    place-items: center;
}
.openingArrow {
    width: 6px;
    aspect-ratio: 1;
    border-top: 2px solid var(--my-blue);
    border-right: 2px solid var(--my-blue);
    transform: rotate(135deg);
    transition: transform 0.5s;
}
.openingArrow.open {
    transform: rotate(-45deg);
}
.template-changer {
    display: flex;
    flex-wrap: wrap;
    gap: 0.3em;
}
.template-changer .preview{
    min-height: 100%;
}
.color-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid transparent;
}
.color-wrapper.active {
    border-color: var(--my-green);
}
.color-changer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 0.2em;
    border-radius: 50%;
}
.color-changer > div {
    width: 1.7em;
    aspect-ratio: 1;
    border-radius: 50%;
    cursor: pointer;
}
.template-color {
    width: 90%;
    aspect-ratio: 1;
    border-radius: 50%;
}
.template {
    height: 85px;
    width: 70px;
    font-size: 2px;
    cursor: pointer;
}
.template.active {
    border: 3px solid var(--my-blue);
}
.input-range__track{
    background-color: var(--my-grey) !important;
} 
.input-range__track--active, .input-range__slider {
    background-color: var(--my-blue) !important;
}
.input-range__label-container {
    display: none !important;
}
.range-input-label-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.range-input {
    font-size: 0.8em;
    font-weight: 600;
}
.file-name {
    height: min-content;
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: flex-start;
}
.file-name > span:focus {
    cursor: text;
    outline: 2px solid var(--my-green);
}
.file-name-helper {
    position: relative;
    width: max-content;
    background-color: var(--my-light-blue);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1em 0.5em ;
    font-weight: 600;
    font-size: .9em;
    cursor: default;
    z-index: 1;
    color: var(--my-cream);
    animation: shake 0.3s linear 1s 2 normal forwards;
    animation: name duration timing-function delay iteration-count direction fill-mode;
}
.file-name-helper::before {
    content: '';
    width: 0.5em;
    height: 0.5em;
    border-left: 3px solid var(--my-cream);
    border-bottom: 3px solid var(--my-cream);
    transform: rotate(45deg);
    position:relative;
    animation: attention 2s linear 0.1s infinite normal forwards;
}
.rail {
    position: absolute;
    height: 1px;
    top: 50%;
    left: 110%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.edit-template-button.mobile {
    display: none;
    height: min-content;
}
.savePdf-container.mobile {
    display: none;
}
.close-template-editor {
    display: none;
}
@keyframes attention {
    0%,50%,100% {left: 0;}
    25%, 75% {left: -0.5em;}
}
@keyframes shake {
    0%, 100% {transform: rotate(0deg);}
    25%, 75% {transform: rotate(2deg);}
    50% {transform: rotate(-2deg);}
}
/* scorll-bar */
::-webkit-scrollbar{
    width: 0.2em;
}
::-webkit-scrollbar-thumb {
    background-color: var(--my-grey);
    border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
    background-color: var(--my-dark-green);
}
::-webkit-scrollbar-track {
    background-color: var(--my-cream);
    border-radius: 10px;
}
::-webkit-scrollbar-button {
    display: none;
}
/* only have hover on desktop */
@media (hover: hover) and (pointer: fine) {
    .moreInfo-button:hover {
        background-color: var(--my-blue);
        color: var(--my-white);
        transition: all 0.5s
    }
    .moreInfo-button:hover .plus-sign {
        transform: scale(1.2);
        transition: transform 0.6s;
    }
    .add-description:hover :last-child{
        text-decoration:underline;
    }
    .summary-button:hover {
        background-color: var(--my-blue);
        color: var(--my-white);
        transition: all 0.6s;
    }
    .educate-desc-title span:hover {
        background-color: #eeeeee;
    }
    .save-options > div:hover {
        background-color: var(--my-white);
        color: var(--my-green);
    }
    .save-options > div:hover > svg {
        transform: scale(1.5);
        transition: transform 0.5s;
    }
    .edit-format-options-container > div:hover {
        background-color: var(--my-cream);
        color: var(--my-light-blue);
    }
    .cv-editBox > div:hover:not(.edit-container){
        background-color: var(--my-light-green);
    }
    

}
@media (width <=630px) {
    .main {
        padding: 0 1em !important;
        width: calc(100% - 2em);
        grid-template-rows: auto 1fr 60px;
    }
    .main-container {
        min-height: calc(100vh - 50px) !important;
    }
    .header {
        font-size: 0.7em;
        color: var(--my-blue);
    }
    .whole-preview:not(.intro-preview), .preview-button{
        display: none;
    }
    .preview-container {
        width: max(160px, 30%);
        align-self: center;
        font-size: 3px;
    }
    .body {
        display: flex;
        flex-direction: column;
    }
    .form {
        gap: .3em;
        justify-content: space-between;
    }
    .foot {
        position: sticky;
        bottom: 0;
        background-color: var(--my-white);
    }
    /* contact-main-page */
    .moreInfoSection {
        margin-top: 25px;
        gap: 25px;
    }
    .moreInfo-button {
        font-size: 1.3em;
        font-weight: 100;
        padding: 0.3em 0.6em;
    }
    .contact-buttons {
        gap: 1.2em;
    }
    /* others-main-page */
    .others-form :first-child, .others-form :nth-child(2) {
        flex: 1 0 100%;
    }
    .others-form {
        height: auto;
    }
    /* summary-page */
    .data-summary {
        width: 100%;
        min-height: 100px;
    }
    .summary-title {
        font-size: 1em;
    }
    .summary-description {
        grid-area: 2 / 1 / 3/ 2;
        font-size: 1em;
    }
    .summary-buttons {
        justify-content: flex-start !important;
        grid-area: 2 / 2 /3/ 3;
    }
    .summary-button {
        width: 30px;
        height: 30px;
    }
    .add-data-container {
        width: 100%;
        border: none;
        font-size: 1.3em;
    }
    /* final-page */
    .file-name, .save-options, .paper-size {
        display: none;
    }
    .pdf-edit-section {
        justify-content: flex-end;
    }
    .edit-template-button.mobile {
        display: flex;
        height: min-content;
        align-items: center;
        cursor: pointer;
        color: var(--my-light-blue);
    }
    .edit-template-button.mobile > svg {
        height: 1em;
    }
    .main-preview-container > .preview {
        grid-area: 2 / 1 / -1 / -1;
        font-size: 0.6em !important;
    }
    .savePdf-container.mobile {
        display: flex;
        justify-content: center;
        width: calc(90% - 3em);
        padding: 1em;
        border-radius: 30px;
        background-color: var(--my-green);
        color: var(--my-white);
        font-weight: 600;
        cursor: pointer;
    }
    .final-main {
        grid-template-rows: auto 1fr auto;
        padding: 0 !important;
        width: 100%;
        height: 100vh;
        /* background-color: var(--my-cream); */
    }
    .final-body {
        padding: 0 1em ;
        width: calc(100% - 2em);
    }
    .final-header {
        display: none;
    }
    .final-foot {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: var(--my-cream);
        padding: 0.3em 0;
    }
    .final-foot .navigation-buttons {
        height: 0;
    }
    .edit-template-container {
        position: fixed;
        width: calc(100% - 2em);
        left: 0;
        bottom: -5px;
        background-color: var(--my-cream) !important;
        box-shadow: 0 -1px var(--my-grey);
        z-index: 1;
        opacity: 0;
        height: 220px;
        animation: comeUp 0.8s linear 0.1ms 1 normal forwards;
        display: grid !important;
        grid-template-columns: 90% 10% !important;
        grid-template-rows: 20% 80%;
        gap: 0;
    }
    .template-changer {
        grid-area: 2 / 1/ 3 /2;
        flex-wrap: nowrap;
        align-items: center;
        justify-content: flex-start;
        overflow: scroll;
    } 
    .template {
        height: 85%;
        width: 110px;
        
    }
    .edit-template-button:not(.edit-template-button.mobile) ,.edit-text-button {
        display: none !important;
    }
    .edit-cv-section {
        height: 0;
        border: none;
    }
    .color-wrapper {
        width: 2.5em !important;
    }
    .close-template-editor {
        justify-self: flex-end;
        align-self: flex-start;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 70%;
        aspect-ratio: 1;
        grid-area: 1 / 2 / 2/ 3;
        cursor: pointer;
    }
    .edit-text-container {
        display: none !important;
    }
}

@keyframes comeUp {
    0% {transform: translate(0, 150px); opacity: 0;}
    25%{opacity: 0;}
    50%{transform:translate(0, 0); opacity: 0.5; }
    70% {transform: translate(0, -5px);opacity: 1;}
    100%{transform: translate(0, 0); opacity: 1;}
}